
<template>
    <section>
        <tg-list :IsShowAction="false" 
        :listType="['List']" 
         ModuleName="Asset" 
         SubModuleCode="Asset"
         IdentityColumn="assetCatalogueId" 
         :PageSize="PageSize"
         :HeaderText="$t('DepreciationList')" 
        :ListData="DepreciationListData" 
        :HeaderData="Headers"
        :widgets="widgets" 
        :callbackfunction="GetDepreciationList" 
        :HeaderButtons="listheaderbuttons" 
        :TotalRecords="TotalRecords"
        :SearchFields="leftSearchFields" 
        :RenderRowActionMethod="renderActions" 
        :ListDataCallBackFunction="GetDepreciationList"
        :LegendArray="LegendArray" 
        :SortExp="SortExp" 
        :SortBy="SortBy" 
        :NorecordfoundText="$t('NorecordfoundText')"
        v-on:LeftsearchButtonClick="leftCommonSearch" 
        v-on:PagerButtonClick="pagerMethod"
        v-on:HeaderButtonClick="commonHeaderButtonClick" 
        v-on:ActionButtonClick="actionButtonClick"
        v-on:SortdataButtonClick="sortdata">
        </tg-list>
    </section>
</template>
<script>
import Schema from "../Asset/DepreciationListSchema.js"
import DataService from '../../service/DataService';
export  default {


 name:'Depreciation',
 components:{
    
 },
 
    data() {
        return{
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            LegendArray: [],
            noRecordForTab:false,
            isLoading:false,
            DepreciationListData:[],
            widgets: [],
            Headers: [{
                "COLUMN_NAME": "itemTypeName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ProductType'),
                "DISPLAY_ORDER": 1,
            },
            {
                "COLUMN_NAME": "itemCode",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ProductCode'),
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "itemName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('ProductName'),
                "DISPLAY_ORDER": 3,
                
            },
            {
                "COLUMN_NAME": "uniqueName",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UniqueName'),
                "DISPLAY_ORDER": 4,
            },
            {
                "COLUMN_NAME": "locationName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Location'),
                "DISPLAY_ORDER": 5,
            },
            {
                "COLUMN_NAME": "usefulLife",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('UsefulLifeYear'),
                "DISPLAY_ORDER": 6,
            },
            {
                "COLUMN_NAME": "percentageOfAssetLife",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('PercentageOfAssetsLife'),
                "DISPLAY_ORDER": 7,
            },
            {
                "COLUMN_NAME": "depreciationMethodName",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('DepreciationRule'),
                "DISPLAY_ORDER": 8,
            },
            {
                "COLUMN_NAME": "deploymentDate",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('InstallDate'),
                "DISPLAY_ORDER": 9,
            },
            {
                "COLUMN_NAME": "quantityOpening",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('QuantityOpening'),
                "DISPLAY_ORDER": 10,                  
            },
            {
                "COLUMN_NAME": "quantityAddition",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('QuantityAddition'),
                "DISPLAY_ORDER": 11,
               
            },
            {
                "COLUMN_NAME": "QuantityDeletion",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('QuantityDeletion'),
                "DISPLAY_ORDER": 12,
                
            },
            {
                "COLUMN_NAME": "quantityClosing",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('QuantityClosing'),
                "DISPLAY_ORDER": 13,
                
            },
            {
                "COLUMN_NAME": "grossBlockOpening",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Opening'),
                "DISPLAY_ORDER": 14,                
            },
            {
                "COLUMN_NAME": "grossBlockAddition",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Addition'),
                "DISPLAY_ORDER": 15,
               
            },
            {
                "COLUMN_NAME": "grossBlockDeletion",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Deletion'),
                "DISPLAY_ORDER": 16,
                
            },
            {
                "COLUMN_NAME": "grossBlockClosing",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('Closing'),
                "DISPLAY_ORDER": 17,
                
            },
            {
                "COLUMN_NAME": "balanceValue",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('BalanceValue'),
                "DISPLAY_ORDER": 18,
               
            },
            {
                "COLUMN_NAME": "depreciationUptoPreviousMonth",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('DepreciationUptoPreviousMonth'),
                "DISPLAY_ORDER": 19,
                
            },
            {
                "COLUMN_NAME": "depreciationOnYTD",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('AccumulatedDepricationonYTD'),
                "DISPLAY_ORDER": 20,
                
            },
            {
                "COLUMN_NAME": "currentBookValue",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t('CurrentbookValue'),
                "DISPLAY_ORDER": 21,
                
            },

            ], 
            productName:'',
            LocationId:'',
            search:'',
            ProductTypeId:'',
            FromDate:'',
            ToDate:'',
            SortBy: '',
            SortExp: '',
            PageSize: 10,
            PageNumber: 1,          
            TotalRecords:0,
            CurrentPage: 1,
            TotalPages: 0,
            privilegeParams:null,
            UserType:null
        }
    },


    created:async function(){
        var vm=this;
        await vm.GetPageSize();
        vm.privilegeParams = `controller=Asset&action=`; 
        await vm.CheckPrivilege(); 
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Product Type");       
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "Location");        
        Schema.bindleftCommonSearchdropdown(vm, "Asset", "DepreciationDate");
        vm.UserType = vm.GetUserInfo.UserType;
    },  
    methods:{  
        async GetPageSize(){            
               var vm =this;
                var params='key=DefaultPageSize'
                await DataService.GetConfigurationValueForCompanyByKey(params).then((response) => {                  
                if(response.data!=null){                                    
                    vm.PageSize = parseInt(response.data);
                }                   
            });
        },
        CheckPrivilege(){
             var vueObj=this;
            if(vueObj.UserType=='CA'){
                DataService.CheckPrivilege(vueObj.privilegeParams + 'Depreciation').then((response)=>{
                if (response.data) {                    
                        vueObj.GetDepreciationList();
                    }else{
                        vueObj.ShowAlert(vueObj.$t('NotAuthorizedSection'), "failure",true, "Alert");
                    }
                });
            }else{
                vueObj.GetDepreciationList();
            } 
           
        },     
      async GetDepreciationList(){            
            var url=`productName=${this.productName}&searchCondition=${this.search}&fromDate=${this.FromDate}&toDate=${this.ToDate}&pageSize=${this.PageSize}&pageNum=${this.PageNumber}&sortBy=${this.SortBy}&sortExp=${this.SortExp}&location_id=${this.LocationId}&productTypeIds=${this.ProductTypeId}`;
            await DataService.GetDepreciationList(url).then(response=>{              
                if(response.data!=null){
                    if(response.data.length>0){                     
                         this.DepreciationListData=response.data;                    
                         this.TotalRecords = response.data[0].totalrecords;
                         this.TotalPages = Math.ceil(this.TotalRecords / this.PageSize);
                         this.CurrentPage = this.PageNumber;
                         this.noRecord = false;
                    }
                    else{
                        this.noRecord = true;
                        this.DepreciationList = [];
                        this.TotalRecorded = 0
                    }                   
                }
            });

        },      
        pagerMethod: function (value) {            
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.GetDepreciationList();
        },
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            }
            else {
                this.searchCondition = "";
            }
            this.FetchData();
        },

        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }

            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },

        renderActions(row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                // switch (action.title) {
                //     case 'Edit':
                //         action.isVisible = true;
                //         break;
                // }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
            case "BacktoList":
                this.OpenBacktoList();
            break;
            }
        },
        actionButtonClick() {

        },
        OpenBacktoList() {
            var vm = this;          
            vm.$router.push({ path: '/AssetCatalog/Index' });
        },


    }
  


}

</script>